.array-container {
	display: inline-block;
	background: rgb(255, 198, 92);
	border: 1px solid white;
	position: relative;

	
}
.array-bar {
	display: inline-block;
	position: relative;
	background: rgb(255, 198, 92);
	
}
.container{
	justify-content: center;

	
}
.nav-bar {
	position: relative;
	top: 80vh;
}
.boot-background {
	background-color: #ffffff;
}
.boot-color {
	color: rgb(195, 146, 223);
}
.boot-hover:hover {
	background-color:rgb(255, 198, 92);
}
.slider1::-webkit-slider-thumb {
	-webkit-appearance: none;
	background: rgb(195, 146, 223);
	cursor: pointer;
  }
.slider1::-webkit-slider-thumb:active{
	background-color:rgb(255, 198, 92);
}
.slider1::-webkit-slider-thumb:focus{
	background-color:rgb(255, 198, 92);
}